import Vue from 'vue'
import Router from 'vue-router'
import Appindex from '@/components/home/Appindex'
import Login from '@/components/Login'
// import Home from '../components/Home'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/index',
      name: 'Appindex',
      component: Appindex
    }
  ]
})



